<template>
  <div class="contactoC">
    <div class="frase">
      <h2 id="contacto">Contacto</h2>
    </div>
    <div class="contacto">
      <form autocomplete="off" @submit.prevent="submitHandler">
        <div class="doble">
          <div class="floating-container">
            <input
              type="text"
              name="nombre"
              placeholder=" "
              required
              @invalid="invalido($event)"
              @input="validar($event)"
              v-model="nombre"
            />
            <label for="nombre">Nombre</label>
          </div>
          <div class="floating-container">
            <input
              type="text"
              name="correo"
              placeholder=" "
              required
              @invalid="invalido($event)"
              @input="validar($event)"
              v-model="correo"
            />
            <label for="correo">Correo</label>
          </div>
        </div>
        <div class="floating-container">
          <input
            type="text"
            name="asunto"
            placeholder=" "
            required
            @invalid="invalido($event)"
            @input="validar($event)"
            v-model="asunto"
          />
          <label for="asunto">Asunto</label>
        </div>
        <div class="floating-container">
          <textarea
            placeholder=" "
            name="mensaje"
            required
            @invalid="invalido($event)"
            @input="validar($event)"
            v-model="mensaje"
          ></textarea>
          <label for="mensaje">Mensaje</label>
        </div>
        <input type="submit" :value="submit" :disabled="enviado"/>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      nombre: "",
      correo: "",
      asunto: "",
      mensaje: "",
      submit:"Enviar",
      enviado:false
    };
  },
  methods: {
    invalido(e) {
      e.target.setCustomValidity("Este campo es obligatorio");
      return;
    },
    validar(e) {
      e.target.setCustomValidity("");
    },
    submitHandler() {
      fetch("https://formsubmit.co/ajax/036ab27218f1e380ef5428030de83e16", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
        body: JSON.stringify({
          name: this.nombre,
          message:this.mensaje,
          mail:this.correo,
          asunto:this.asunto
        }),
      })
        .then((response) => response.json())
        .then((data) => console.log(data))
        .catch((error) => console.log(error));
      this.enviado=true;
      this.submit="Mensaje enviado"
    },
  },
};
</script>

<style scoped>
.contacto {
  width: 70%;
  margin: auto;
}
form {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
form input[type="text"],
textarea {
  height: 40px;
  margin-bottom: 10px;
  flex: 1;
  padding: 5px;
  border: 2px solid #ebebeb;
  border-radius: 5px;
  outline: none;
  background-color: transparent;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: white;
  width: 100%;
}
form input[type="text"]:focus,
textarea:focus {
  border-color: #1a97cf;
}
form input::placeholder,
textarea::placeholder {
  color: rgba(255, 255, 255, 0.658);
}
form .doble {
  display: flex;
  gap: 20px;
}
textarea {
  min-height: 180px;
  resize: none;
}
form input[type="submit"] {
  margin-top: 20px;
  border-radius: 10px;
  padding: 2px 40px;
  height: 30px;
  align-self: center;
  outline: none;
  cursor: pointer;
  background-color: #1a97cf;
  color: white;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border: none;
  font-weight: 400;
}

form input[type="submit"]:active {
  box-shadow: inset 0 0.6em 2em -0.3em rgba(0, 0, 0, 0.15),
    inset 0 0 0em 0.05em rgba(255, 255, 255, 0.12);
}
form input[type="submit"]:focus {
 box-shadow:0px 0px 0em 0.2em rgb(0 0 0 / 24%)
}
input[type="submit"]:disabled {
  background: #41b883;
}
.floating-container {
  position: relative;
  flex: 1;
}
label {
  position: absolute;
  left: 6px;
  top: 10px;
  color: white;
  opacity: 0.7;
  transition: all 0.5s ease;
}

form input[type="text"]:focus + label,
form textarea:focus + label {
  background: #252734;
  transform: translateY(-20px);
  opacity: 1;
}
form input[type="text"]:not(:placeholder-shown) + label,
form textarea:not(:placeholder-shown) + label {
  background: #252734;
  transform: translateY(-20px) scale(0.9);
  opacity: 1;
}

@media (max-width: 772px) {
  form .doble {
    flex-flow: column;
    gap: 0;
  }
}
</style>